<template>
    <div class="page-index">
        <div class="page-index-one" @click="goto('one', one.id)">
            <img class="onemg" :src="one.img" alt="">
            <div class="one-detail">
                <div class="one-detail-day">
                    <span class="day">{{one.day}}</span>
                    <span>{{one.month}}</span>
                </div>
                <div class="one-detail-main">
                    <p>{{one.content}}</p>
                </div>
            </div>
        </div>
        <div class="page-index-article">
            <h1 style="font-size:.5rem;">ARTICLE <span>{{article.num}}</span></h1>
            <h1 style="font-size:.3rem;text-decoration:under-line"
            @click="goto('article', article.id)"
            >{{article.title.split('-')[0]}}-<span>{{article.title.split('-')[1]}}</span></h1>
        </div>
        <div class="page-index-question">
            <h1 style="font-size:.5rem;">QUESTION <span>{{question.num}}</span></h1>
            <h1 style="font-size:.3rem;text-decoration:under-line"
            @click="goto('question', question.id)"
            >{{question.title}}</h1>
        </div>
    </div>
</template>
<script>
import { mapState, mapActions } from 'vuex'
import { getToday } from '../api.config'
export default {
    name:'index',
    metaInfo(){
        return {
            title: 'ONE',
        }
    },
    data(){
        return {
            article:{
                title:''
            },
            one:{},
            question:{},
        }
    },
    mounted(){
        this.Today()
    },
    methods:{
        Today(){
            getToday().then(res => {
                this.article = res.data.article
                this.one = res.data.one 
                this.question = res.data.question
            })
        },
        goto(name,id){
            this.$router.push({
                name:name,
                params:{
                    id:id
                }
            })
        }
    }
}
</script>
<style lang="stylus" scoped>
.page-index 
    position relative
    &-one 
        padding 0rem
        img.onemg
            width 100%
            display block
    .one-detail
        display flex 
        justify-content space-between
        position relative
        &-day 
            padding .2rem
            display flex 
            justify-content space-between
            flex-direction column
            text-align center
            .day
                font-size .5rem
                font-weight bold
        &-main
            position relative
            flex 1
            padding .3rem
            p
                margin 0
            span 
                position absolute
                bottom 0
                right .3rem
        &:after 
            content ""
            display block
            width 100%
            border-bottom 1px solid #000
            position absolute
            bottom 0
            transform scaleY(.5)
    &-article
        padding .2rem
    &-question
        padding .2rem
</style>




